/** @jsx jsx */
import { jsx, Box, Button } from 'theme-ui'
import React from 'react'
// import Layout from '../../templates/layouts/Layout'
import ToggleSwitch from '../../../../../../gatsby-theme-jim-components/src/components/product/designElements/inputs/ToggleSwitch/ToggleSwitch'

const ReactTestingPage = (props) => (
	<>
		<Box
			{...props}
			sx={{
				mt: '200px',
				ml: '200px',
				zIndex: 10,
			}}
		>
			<ToggleSwitch />
			<Button />
		</Box>
	</>
)

export default ReactTestingPage
